import React, { useEffect, useState } from "react";
import Button from "./components/Container";
import Container from "./components/Container";
import Text from "./components/Text";

type IPlayer = {
  playerName?: string;
  choice?: "Circle" | "X";
  wins?: number;
};

function App() {
  const [firstPlayer, setFirstPlayer] = useState<IPlayer>();
  const [secondPlayer, setSecondPlayer] = useState<IPlayer>();
  const [turn, setTurn] = useState<IPlayer | undefined>(undefined);
  const [winner, setWinner] = useState<{ player: IPlayer; index: number }[]>();
  const [ties, setTies] = useState(0);
  const [selectedChoice, setSelectedChoice] =
    useState<IPlayer["choice"]>("Circle");
  const [positions, setPositions] = useState<
    [
      IPlayer,
      IPlayer,
      IPlayer,
      IPlayer,
      IPlayer,
      IPlayer,
      IPlayer,
      IPlayer,
      IPlayer
    ]
  >([{}, {}, {}, {}, {}, {}, {}, {}, {}]);

  const CheckIfWins = () => {
    //Check horizontal
    positions.map((value, index) => {
      let Elements = [];
      if (!winner) {
        if (index === 0 || index === 3 || index === 6) {
          for (let h = index; h <= index + 2; h++) {
            Elements.push({ player: positions[h], index: h });
          }
          if (Elements.every((val, i, arr) => val.player === arr[0].player)) {
            setWinner(Elements);
          }
        }
        Elements = [];

        //Check vertical
        if (index === 0 || index === 1 || index === 2) {
          for (let h = index; h <= index + 6; h = h + 3) {
            Elements.push({ player: positions[h], index: h });
          }
          if (Elements.every((val, i, arr) => val.player === arr[0].player)) {
            setWinner(Elements);
          }
        }
        Elements = [];

        //Check 45
        if (index === 0) {
          for (let h = index; h <= index + 8; h = h + 4) {
            Elements.push({ player: positions[h], index: h });
          }
          if (Elements.every((val, i, arr) => val.player === arr[0].player)) {
            setWinner(Elements);
          }
        }
        Elements = [];

        //Check 90
        if (index === 2) {
          for (let h = index; h <= index + 5; h = h + 2) {
            Elements.push({ player: positions[h], index: h });
          }
          if (Elements.every((val, i, arr) => val.player === arr[0].player)) {
            setWinner(Elements);
          }
        }
      }
    });
  };

  useEffect(() => {
    if (firstPlayer && secondPlayer) {
      setTurn(turn === firstPlayer ? secondPlayer : firstPlayer);
      CheckIfWins();
    }
  }, [positions]);

  useEffect(() => {
    if (winner && firstPlayer && secondPlayer) {
      const newWinner =
        winner[0].player.playerName === firstPlayer.playerName
          ? setFirstPlayer({
              ...firstPlayer,
              wins: firstPlayer.wins ? firstPlayer.wins + 1 : 1,
            })
          : setSecondPlayer({
              ...secondPlayer,
              wins: secondPlayer.wins ? secondPlayer.wins + 1 : 1,
            });

      console.log("new", newWinner);
    }
  }, [winner]);

  useEffect(() => {
    if (firstPlayer) {
      setTurn(firstPlayer);
    }
  }, [firstPlayer]);

  type IconsProps = {
    scale: string;
    background: string;
  };

  const CircleIcon: React.FC<IconsProps> = (props: IconsProps) => {
    return (
      <Container
        height={props.scale}
        width={props.scale}
        borderRadius={"50%"}
        background={props.background}
        justifyContent={"center"}
        alignItems="center"
      >
        <Container
          height={"50%"}
          width={"50%"}
          borderRadius={"50%"}
          background={"#1F3540"}
        />
      </Container>
    );
  };

  const XIcon: React.FC<IconsProps> = (props: IconsProps) => {
    return (
      <Container
        height={props.scale}
        width={props.scale}
        justifyContent={"center"}
        alignItems="center"
        position={"relative"}
      >
        <Container
          position={"absolute"}
          height={"100%"}
          width={"20%"}
          borderRadius={"4px"}
          background={props.background}
          transform={"rotate(-45deg)"}
        />
        <Container
          position={"absolute"}
          height={"100%"}
          width={"20%"}
          borderRadius={"4px"}
          background={props.background}
          transform={"rotate(45deg)"}
        />
      </Container>
    );
  };

  return (
    <Container
      width={"100%"}
      height={"100%"}
      background={"#192A32"}
      justifyContent={"center"}
      alignItems={"center"}
    >
      <Container width={"500px"} height={"515"} flexDirection={"column"}>
        <Container
          width={"100%"}
          height={"38px"}
          marginBottom={"22px"}
          display={"grid"}
          gridTemplateColumns={"1fr 1fr 1fr"}
          gap={"18px"}
        >
          <Container width={"76px"} height={"100%"}>
            <Container flex={"1"} height={"100%"} alignItems="center">
              <XIcon scale="90%" background="#31C4BE" />
            </Container>
            <Container flex={"1"} height={"100%"} alignItems="center">
              <CircleIcon scale="90%" background="#F2B237" />
            </Container>
          </Container>
          <Container
            width={"154px"}
            height={"100%"}
            background={"#1F3540"}
            borderRadius={"8px"}
            boxShadow={"0px 4px 4px rgba(0, 0, 0, 0.25);"}
            alignItems={"center"}
            justifyContent={"center"}
          >
            {turn && turn.choice === "Circle" ? (
              <CircleIcon scale="20px" background="#8197A1" />
            ) : (
              <XIcon scale="20px" background="#8197A1" />
            )}
            <Text color="#8197A1" fontSize={"18px"} marginLeft={"8px"}>
              TURN
            </Text>
          </Container>
          <Container justifyContent={"flex-end"}>
            <Container
              background={"#A8BEC9"}
              width={"38px"}
              height={"100%"}
              borderRadius={"8px"}
              onClick={() => {
                setPositions([{}, {}, {}, {}, {}, {}, {}, {}, {}]);
              }}
              alignItems={"center"}
              justifyContent={"center"}
            >
              <Text color="#1F3540" fontSize={"18px"}>
                R
              </Text>
            </Container>
          </Container>
        </Container>

        <Container
          display={"grid"}
          gridTemplateColumns={"1fr 1fr 1fr"}
          gap={"18px"}
        >
          {positions.map((position, key) => (
            <Container
              key={key}
              width={"100%"}
              height={"154px"}
              background={
                winner?.find((value) => value.index === key)
                  ? winner[0].player.choice === "X"
                    ? "#31C4BE"
                    : "#F2B237"
                  : "#1F3540"
              }
              borderRadius={"8px"}
              boxShadow={"0px 4px 4px rgba(0, 0, 0, 0.25);"}
              justifyContent={"center"}
              alignItems={"center"}
              onClick={() => {
                if (
                  positions[key] !== firstPlayer &&
                  positions[key] !== secondPlayer &&
                  turn
                ) {
                  let newArr = positions;
                  newArr[key] = turn;
                  setPositions([...newArr]);
                }
              }}
            >
              {position?.choice === "Circle" ? (
                <CircleIcon
                  scale="60%"
                  background={
                    winner && winner[0].player.choice === "Circle"
                      ? "#1F3540"
                      : "#F2B237"
                  }
                />
              ) : position.choice === "X" ? (
                <XIcon
                  scale="60%"
                  background={
                    winner && winner[0].player.choice === "X"
                      ? "#1F3540"
                      : "#31C4BE"
                  }
                />
              ) : (
                <></>
              )}
            </Container>
          ))}
        </Container>

        <Container height={"65px"} width={"100%"} marginTop={"22px"}>
          <Container
            flex={1}
            marginRight={"18px"}
            background={"#31C4BE"}
            borderRadius={"8px"}
            alignItems={"center"}
            justifyContent={"center"}
            flexDirection={"column"}
          >
            <Text color="#1F3540" fontSize={"14px"}>
              X ({" "}
              {firstPlayer?.choice === "X"
                ? firstPlayer?.playerName
                : secondPlayer?.playerName}{" "}
              )
            </Text>
            <Text color="#1F3540" fontSize={"22px"}>
              {firstPlayer?.choice === "X"
                ? firstPlayer?.wins
                : secondPlayer?.wins}
            </Text>
          </Container>
          <Container
            flex={1}
            marginRight={"18px"}
            background={"#A8BEC9"}
            borderRadius={"8px"}
            alignItems={"center"}
            justifyContent={"center"}
            flexDirection={"column"}
          >
            <Text color="#1F3540" fontSize={"14px"}>
              TIES
            </Text>
            <Text color="#1F3540" fontSize={"22px"}>
              {ties + 1}
            </Text>
          </Container>
          <Container
            flex={1}
            background={"#F2B237"}
            borderRadius={"8px"}
            alignItems={"center"}
            justifyContent={"center"}
            flexDirection={"column"}
          >
            <Text color="#1F3540" fontSize={"14px"}>
              O ({" "}
              {firstPlayer?.choice === "Circle"
                ? firstPlayer?.playerName
                : secondPlayer?.playerName}{" "}
              )
            </Text>
            <Text color="#1F3540" fontSize={"22px"}>
              {firstPlayer?.choice === "Circle"
                ? firstPlayer?.wins
                : secondPlayer?.wins}
            </Text>
          </Container>
        </Container>
      </Container>

      <Container
        position={"absolute"}
        width={"100vw"}
        height={"100vh"}
        background={"#192A32"}
        alignItems={"center"}
        justifyContent={"center"}
        overflow={"hidden"}
        maxWidth={!firstPlayer || !secondPlayer ? "100vw" : "0vw"}
        flexDirection={"column"}
      >
        <Container marginBottom={"24px"}>
          <XIcon scale={"40px"} background={"#33C2BF"} />
          <CircleIcon scale={"40px"} background={"#F2B237"} />
        </Container>
        <Container
          width={"480px"}
          padding={"24px"}
          background={"#1F3540"}
          borderRadius={"8px"}
          alignItems={"center"}
          justifyContent={"center"}
          flexDirection={"column"}
        >
          <Text fontSize={"14px"} color={"#A8BEC9"} marginBottom={"18px"}>
            PICK PLAYER 1'S MARK
          </Text>

          <Container
            background={"#192A32"}
            borderRadius={"8px"}
            width={"100%"}
            height={"58px"}
            padding={"8px"}
            marginBottom={"18px"}
          >
            <Container
              flex={1}
              background={selectedChoice === "X" ? "#A8BEC9" : "#192A32"}
              borderRadius={"8px"}
              justifyContent={"center"}
              alignItems={"center"}
              onClick={() => setSelectedChoice("X")}
            >
              <XIcon
                background={selectedChoice === "X" ? "#192A32" : "#A8BEC9"}
                scale={"42px"}
              />
            </Container>
            <Container
              flex={1}
              background={selectedChoice === "Circle" ? "#A8BEC9" : "#192A32"}
              borderRadius={"8px"}
              justifyContent={"center"}
              alignItems={"center"}
              onClick={() => setSelectedChoice("Circle")}
            >
              <CircleIcon
                background={selectedChoice === "Circle" ? "#192A32" : "#A8BEC9"}
                scale={"42px"}
              />
            </Container>
          </Container>

          <Text fontSize={"10px"} color={"#A8BEC9"}>
            REMEMBER X GOES FIRST
          </Text>
        </Container>

        <Button
          background={"#F2B237"}
          padding={"12px"}
          borderRadius={"8px"}
          boxShadow={"0px 4px 4px rgba(0, 0, 0, 0.25);"}
          width={"502px"}
          alignItems={"center"}
          justifyContent={"center"}
          marginTop={"24px"}
          onClick={() => {}}
        >
          <Text fontSize={"18px"} color={"#1D323D"}>
            NEW GAME (VS CPU)
          </Text>
        </Button>

        <Button
          background={"#33C2BF"}
          padding={"12px"}
          borderRadius={"8px"}
          boxShadow={"0px 4px 4px rgba(0, 0, 0, 0.25);"}
          width={"502px"}
          alignItems={"center"}
          justifyContent={"center"}
          marginTop={"24px"}
          onClick={() => {
            setFirstPlayer({
              choice: selectedChoice,
              playerName: "Player 1",
              wins: 0,
            });

            setSecondPlayer({
              choice: selectedChoice === "X" ? "Circle" : "X",
              playerName: "Player 2",
              wins: 0,
            });
          }}
        >
          <Text fontSize={"18px"} color={"#1D323D"}>
            NEW GAME (VS PLAYER)
          </Text>
        </Button>
      </Container>

      <Container
        position={"absolute"}
        width={"100vw"}
        height={"100vh"}
        background={"rgba(0, 0, 0, 0.3)"}
        alignItems={"center"}
        justifyContent={"center"}
        overflow={"hidden"}
        maxWidth={winner ? "100vw" : "0vw"}
      >
        <Container
          background={"#1F3540"}
          width={"100%"}
          alignItems={"center"}
          justifyContent={"center"}
          flexDirection={"column"}
          padding={"52px"}
        >
          <Text fontSize={"18px"} color={"#8197A1"} marginBottom={"24px"}>
            {winner && winner[0].player.playerName?.toUpperCase()} WON THIS
            ROUND!
          </Text>
          <Container
            width={"100%"}
            alignItems="center"
            justifyContent={"center"}
            marginBottom={"24px"}
          >
            {winner && winner[0].player.choice === "X" ? (
              <XIcon scale="70px" background="#33C2BF" />
            ) : (
              <CircleIcon scale="70px" background="#F2B237" />
            )}
            <Text
              fontSize={"48px"}
              color={
                winner && winner[0].player.choice === "X"
                  ? "#33C2BF"
                  : "#F2B237"
              }
              marginLeft={"18px"}
            >
              TAKES THE ROUND
            </Text>
          </Container>

          <Container>
            <Button
              marginRight={"16px"}
              background={"#8197A1"}
              padding={"12px"}
              borderRadius={"8px"}
              boxShadow={"0px 4px 4px rgba(0, 0, 0, 0.25);"}
              onClick={() => {
                setFirstPlayer(undefined);
                setSecondPlayer(undefined);
              }}
            >
              <Text fontSize={"18px"} color={"#1D323D"}>
                QUIT
              </Text>
            </Button>
            <Button
              background={"#F5B43B"}
              padding={"12px"}
              borderRadius={"8px"}
              boxShadow={"0px 4px 4px rgba(0, 0, 0, 0.25);"}
              onClick={() => {
                setWinner(undefined);
                setPositions([{}, {}, {}, {}, {}, {}, {}, {}, {}]);
                setTies(ties + 1);
              }}
            >
              <Text fontSize={"18px"} color={"#1D323D"}>
                NEXT ROUND
              </Text>
            </Button>
          </Container>
        </Container>
      </Container>

      <Container
        position={"absolute"}
        bottom={16}
        right={16}
        onClick={() => {}}
      >
        <Text fontSize={"14px"} color={"#8197A1"}>
          Como jogar?
        </Text>
      </Container>
    </Container>
  );
}

export default App;
